@import '../common-imports';

.page-404{
    header{
        max-width:32.4rem;
        padding: 10rem 0;
        @include media-breakpoint-up(lg) {
            padding: 24.6rem 0;
        }
    }
    &__number{
        width: 100%;
        height: 30rem;
        bottom: 0;
        right: 0;
        @include media-breakpoint-up(lg) {
            position: absolute;
            height: 100%;
            width: 50%;
        }
    }
}
